.CompanyProfileSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 25px 0px;
}

.mainDivisionComProfile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 1350px;
  padding: 25px 0px;
}

.mainDivisionComProfile h1:nth-child(1) {
  margin-top: 0;
}

.mainDivisionComProfile h1 {
  font-size: 24px;
  font-weight: 600;
  color: rgb(40, 40, 40);
  width: 100%;
  margin-top: 25px;
}
.mainDivisionComProfile p {
  width: 100%;
  font-weight: 400;
  font-size: 14px;
  margin-top: 5px;
  line-height: 22px;
}

/* Mission Vision Values Goals Section */

.mvvg_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mainDivision_mvvg_Section {
  display: flex;
  justify-content: center;
  width: 1350px;
}

.leftDiv_mvvg {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 30%;
  background-color: #e5302f;
  background-color: rgb(40, 40, 40);
  padding: 20px;
  border-radius: 25px;
  color: white;
}

.leftDiv_mvvg h1 {
  width: 100%;
  font-size: 36px;
  line-height: 40px;
}
.leftDiv_mvvg p {
  width: 100%;
  font-size: 14px;
  font-weight: 300;
}

.rightDiv_mvvg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  flex-wrap: wrap;
  background-color: white;
}

.rightDiv_mvvg .mvvg_Divs {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 40%;
  padding: 10px;
}

.rightDiv_mvvg .mvvg_Divs img {
  max-width: fit-content;
  max-height: 100px;
}

.rightDiv_mvvg .mvvg_Divs h1 {
  font-size: 24px;
  color: rgb(60, 60, 60);
}
.rightDiv_mvvg .mvvg_Divs p {
  font-size: 14px;
}

/* Contact Div Styling */

.imgDiv_mvvg_Section {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 500px;
  width: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("../Images/aboutUsMVVG.jpg");
}

/* End of Mission Visison Values Goals Section */

/* Contact Us Div Styling */

.imgDiv_mvvg_Section h1 {
  color: rgb(230, 230, 230);
  font-weight: 400;
  font-size: 36px;
  width: 1350px;
  text-align: center;
}

.imgDiv_mvvg_Section p {
  color: rgb(230, 230, 230);
  font-size: 15px;
  width: 1350px;
  text-align: center;
  font-weight: 300;
}

.imgDiv_mvvg_Section .btnsDiv_Hero {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px 0px;
  width: 100%;
  gap: 25px;
}
.imgDiv_mvvg_Section .btnsDiv_Hero a {
  text-decoration: none;
}
.imgDiv_mvvg_Section .btnsDiv_Hero .callNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: white;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 400;
}

.imgDiv_mvvg_Section .btnsDiv_Hero .callNowBTN_Hero:hover {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(50, 50, 50);
  color: white;
}

.imgDiv_mvvg_Section .btnsDiv_Hero .whatsAppNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid #25d366;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #25d366;
  color: white;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.imgDiv_mvvg_Section .btnsDiv_Hero .whatsAppNowBTN_Hero:hover {
  background-color: #198d43;
  border: 2px solid #198d43;
}

/* End of Contact Us Div Styling */

/* Responsiveness of Website MVVG SECTION */

@media screen and (max-width: 1350px) {
  .mvvg_Section {
    padding: 0px 25px;
  }
}

@media screen and (max-width: 1000px) {
  .leftDiv_mvvg {
    width: 40%;
  }
  .rightDiv_mvvg {
    width: 60%;
  }
  .mvvg_Divs {
    width: 50%;
  }
}

@media screen and (max-width: 800px) {
  .mvvg_Section {
    padding: 0;
  }
  .mainDivision_mvvg_Section {
    flex-direction: column;
  }
  .leftDiv_mvvg {
    width: 100%;
    border-radius: 0;
  }
  .leftDiv_mvvg h1 {
    font-size: 28px;
    line-height: 30px;
  }
  .rightDiv_mvvg {
    width: 100%;
    border-radius: 0;
    background-color: rgb(40, 40, 40);
    color: rgb(230, 230, 230);
  }
  .rightDiv_mvvg .mvvg_Divs h1 {
    color: rgb(230, 230, 230);
  }
}

@media screen and (max-width: 500px) {
  .imgDiv_mvvg_Section {
    margin-top: 0;
  }
  .rightDiv_mvvg {
    flex-direction: column;
    flex-wrap: wrap;
    /* border: 1px solid red; */
  }
  .rightDiv_mvvg .mvvg_Divs {
    height: fit-content;
    width: 100%;
    /* border: 1px solid red; */
  }
}

/* End of Responsiveness of Website */

/* Responsiveness of Website Company Profile Section  */

@media screen and (max-width: 1350px) {
  .CompanyProfileSection {
    padding: 25px;
  }

  .mainDivisionComProfile h1 {
    line-height: 30px;
    font-size: 22px;
    color: rgb(100, 100, 100);
  }
  .mainDivisionComProfile p {
    color: rgb(130, 130, 130);
    font-size: 13px;
  }
}

/* End of Responsiveness of Website Company Profile Section  */

/* Responsiveness of Website Company Contact Us Section  */

@media screen and (max-width: 1350px) {
  .imgDiv_mvvg_Section {
    padding: 10px;
  }
  .imgDiv_mvvg_Section h1 {
    width: 100%;
    font-size: 30px;
    font-weight: 600;
  }
  .imgDiv_mvvg_Section p {
    width: 100%;
  }

  .imgDiv_mvvg_Section .btnsDiv_Hero .callNowBTN_Hero {
    padding: 6px 25px;
    border-radius: 5px;
    font-size: 13px;
  }

  .imgDiv_mvvg_Section .btnsDiv_Hero .whatsAppNowBTN_Hero {
    padding: 6px 25px;
    border-radius: 5px;
    font-size: 13px;
  }
}

/* End of Responsiveness of Website Contact Us Section  */
