.AllProductPage_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 25px 0px;
}

.mainDivisionProductPage_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  flex-direction: column;
  gap: 50px;
}

.mainDivisionProductPage_Section h1 {
  font-size: 36px;
  color: rgb(80, 80, 80);
}

.mainDivisionProductPage_Section span {
  text-align: left;
  width: 100%;
  color: rgb(80, 80, 80);
}

.mainDivisionProductPage_Section span a {
  text-decoration: none;
  color: rgb(80, 80, 80);
}

/* Responsiveness of Website */

@media screen and (max-width: 1350px) {
  .mainDivisionProductPage_Section {
    width: 100%;
    padding: 25px;
  }
}

/* End of Responsiveness of Website */
