.ContactPageSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}

.getInTouch_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 25px 0px;
  background-color: rgb(20, 20, 20);
  color: white;
  color: rgb(230, 230, 230);
}

.mainDivision_getInTouch_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  padding: 25px 0px;
}

.mainDivision_getInTouch_Section .gIT_divs {
  width: 30%;
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.mainDivision_getInTouch_Section .gIT_divs span:nth-child(2) {
  margin-top: 25px;
}

.mainDivision_getInTouch_Section .gIT_divs span {
  text-align: center;
  width: 100%;
}

.mainDivision_getInTouch_Section .gIT_divs p {
  text-align: center;
  width: 100%;
}

.mainDivision_getInTouch_Section .gIT_divs a {
  text-decoration: none;
  color: rgb(220, 220, 220);
}

/* Map Section  */

.mapSection {
  width: 100%;
}

/* End of Map Section  */

.ContactPageSection .imgDiv_mvvg_Section {
  margin-top: 0;
}

/* Responsivenss of Contact Us Section */

@media screen and (max-width: 550px) {
  .getInTouch_Section {
    padding: 25px;
  }

  .mainDivision_getInTouch_Section {
    flex-direction: column;
  }
  .mainDivision_getInTouch_Section .gIT_divs {
    width: 100%;
    margin: 30px;
  }
}

/* End of Responsivenss of Contact Us Section */
