.Hero_Section {
  width: 100%;
  height: 100vh;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../Images/homeHeroBGImage.jpg");
  background-size: cover;

  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainDivision_HeroSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 1350px;
}
.mainDivision_HeroSection h1 {
  width: 100%;
  text-align: center;
  font-weight: 600;
  letter-spacing: 1px;
  line-height: 60px;
  font-size: 60px;
  color: rgb(235, 235, 235);
}
.mainDivision_HeroSection p {
  color: rgb(235, 235, 235);
  width: 100%;
  text-align: center;
  font-size: 16px;
  font-weight: 300;
}

.mainDivision_HeroSection .btnsDiv_Hero {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 25px 0px;
  width: 100%;
  gap: 25px;
}
.mainDivision_HeroSection .btnsDiv_Hero a {
  text-decoration: none;
}
.mainDivision_HeroSection .btnsDiv_Hero .callNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid white;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: white;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 400;
}

.mainDivision_HeroSection .btnsDiv_Hero .callNowBTN_Hero:hover {
  background-color: rgb(50, 50, 50);
  border: 2px solid rgb(50, 50, 50);
  color: white;
}

.mainDivision_HeroSection .btnsDiv_Hero .whatsAppNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid #25d366;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #25d366;
  color: white;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.mainDivision_HeroSection .btnsDiv_Hero .whatsAppNowBTN_Hero:hover {
  background-color: #198d43;
  border: 2px solid #198d43;
}

/* Hero Section Styling Responsiveness */

@media screen and (max-width: 1350px) {
  .Hero_Section {
    padding: 25px;
  }

  .mainDivision_HeroSection {
    padding: 0;
    width: 100%;
  }
}

@media screen and (max-width: 1000px) {
  .Hero_Section {
    height: 80vh;
  }

  .mainDivision_HeroSection h1 {
    font-size: 36px;
    line-height: 36px;
  }
  .mainDivision_HeroSection p {
    font-size: 13px;
  }
}

@media screen and (max-width: 500px) {
  .mainDivision_HeroSection .btnsDiv_Hero .callNowBTN_Hero {
    padding: 6px 25px;
    border-radius: 5px;
    font-size: 13px;
  }
  .mainDivision_HeroSection .btnsDiv_Hero .whatsAppNowBTN_Hero {
    padding: 6px 25px;
    border-radius: 5px;
    font-size: 13px;
  }
  .mainDivision_HeroSection h1 {
    font-size: 30px;
    line-height: 35px;
  }
  .mainDivision_HeroSection p {
    font-size: 13.5px;
  }
}

/* End of Hero Section Styling Responsiveness */
