.ProductCardSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mainDivisionProductCardSection {
  display: flex;
  justify-content: center;
  width: 1350px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
}

.mainDivisionProductCardSection .imgDiv_productCard {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
}
.mainDivisionProductCardSection .imgDiv_productCard a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mainDivisionProductCardSection .imgDiv_productCard img {
  width: 100%;
  height: 100%;
}

.mainDivisionProductCardSection .contentDiv_productCard {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 75%;
  padding: 25px;
}

.mainDivisionProductCardSection .contentDiv_productCard h1 {
  font-size: 22px;
  color: rgb(80, 80, 80);
}
.mainDivisionProductCardSection .contentDiv_productCard p {
  margin-top: 5px;
  font-size: 14px;
  color: rgb(110, 110, 110);
}
.mainDivisionProductCardSection .contentDiv_productCard span {
  margin-top: 15px;
  font-size: 15px;
}

.mainDivisionProductCardSection .contentDiv_productCard .btnsDiv_pCard {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  gap: 25px;
  padding: 25px 0px;
}

.mainDivisionProductCardSection .contentDiv_productCard .btnsDiv_pCard a {
  text-decoration: none;
}
.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .callNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid black;
  font-size: 14px;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: black;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 400;
}

.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .callNowBTN_Hero:hover {
  background-color: #e5302f;
  border: 2px solid #e5302f;
  color: white;
}

.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .whatsAppNowBTN_Hero {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid #25d366;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #25d366;
  color: white;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .whatsAppNowBTN_Hero:hover {
  background-color: #198d43;
  border: 2px solid #198d43;
}

.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .learnMore_BTN {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid #e5302f;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: transparent;
  color: white;
  color: #e5302f;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.mainDivisionProductCardSection
  .contentDiv_productCard
  .btnsDiv_pCard
  .learnMore_BTN:hover {
  color: white;
  background-color: black;
  border: 2px solid black;
}

/* Responsiveness  */

@media screen and (max-width: 1350px) {
  .mainDivisionProductCardSection {
    width: 100%;
    padding: 25px;
  }
  .mainDivisionProductCardSection .imgDiv_productCard img {
    width: 100%;
    height: fit-content;
  }
}

@media screen and (max-width: 900px) {
  .mainDivisionProductCardSection .imgDiv_productCard {
    width: 35%;
  }

  .mainDivisionProductCardSection .contentDiv_productCard {
    width: 65%;
  }
}

@media screen and (max-width: 900px) {
  .mainDivisionProductCardSection .imgDiv_productCard {
    width: 35%;
  }

  .mainDivisionProductCardSection .contentDiv_productCard {
    width: 65%;
  }
}

@media screen and (max-width: 800px) {
  .mainDivisionProductCardSection {
    flex-direction: column;
  }
  .mainDivisionProductCardSection .imgDiv_productCard {
    width: 100%;
  }

  .mainDivisionProductCardSection .contentDiv_productCard {
    width: 100%;
  }
}

@media screen and (max-width: 700px) {
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .callNowBTN_Hero {
    padding: 5px 20px;
    font-size: 13px;
  }
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .whatsAppNowBTN_Hero {
    padding: 5px 20px;
    font-size: 13px;
  }

  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .learnMore_BTN {
    padding: 5px 20px;
    font-size: 13px;
  }
}

@media screen and (max-width: 500px) {
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .callNowBTN_Hero {
    padding: 6px 25px;
    font-size: 14px;
    gap: 10px;
    width: fit-content;
  }
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .whatsAppNowBTN_Hero {
    padding: 6px 25px;
    font-size: 14px;
    gap: 10px;
    width: fit-content;
  }

  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .learnMore_BTN {
    padding: 6px 25px;
    font-size: 14px;
    gap: 10px;
    width: fit-content;
  }
  .mainDivisionProductCardSection .contentDiv_productCard .btnsDiv_pCard {
    justify-content: space-around;
  }

  .hideDivLink {
    display: none;
  }
}

@media screen and (max-width: 400px) {
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .callNowBTN_Hero {
    padding: 5px 15px;
    font-size: 13px;
    gap: 5px;
  }
  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .whatsAppNowBTN_Hero {
    padding: 5px 15px;
    font-size: 13px;
    gap: 5px;
  }

  .mainDivisionProductCardSection
    .contentDiv_productCard
    .btnsDiv_pCard
    .learnMore_BTN {
    padding: 5px 15px;
    font-size: 13px;
    gap: 5px;
  }
  .mainDivisionProductCardSection .contentDiv_productCard .btnsDiv_pCard {
    justify-content: space-between;
    width: 100%;
  }
  .mainDivisionProductCardSection .contentDiv_productCard {
    padding: 0;
  }
}

/* End of Responsiveness  */
