.Blog_Update_Section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}

.mainDivision_BlogUpdate {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  flex-direction: column;
}

.mainDivision_BlogUpdate label {
  width: 50%;
  margin-top: 25px;
  font-size: 15px;
  font-weight: 500;
  color: rgb(80, 80, 80);
}
.mainDivision_BlogUpdate textarea {
  padding: 5px 5px;
  outline: none;
  border-radius: 5px;
  border: 1px solid rgb(20, 20, 20);
  width: 50%;
  max-width: 50%;
  font-size: 14px;
  color: rgb(40, 40, 40);
}

.mainDivision_BlogUpdate .btnDiv_updateBlog {
  width: 50%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.mainDivision_BlogUpdate button {
  margin: 25px 0px;
  padding: 10px 30px;
  background-color: black;
  color: white;
  border: 2px solid black;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s;
  font-size: 15px;
}

.mainDivision_BlogUpdate button:hover {
  background-color: #e5302f;
  color: white;
  border: 2px solid #e5302f;
}
