.BlogDisplaySection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 15px 0px;
}

.mainDivision_BlogDisplaySection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  padding: 15px 0px;
  flex-direction: column;
}

.mainDivision_BlogDisplaySection h4 {
  color: rgb(100, 100, 100);
  font-size: 16px;
  font-weight: 500;
  width: 100%;
}

.mainDivision_BlogDisplaySection h4 a {
  text-decoration: none;
  color: rgb(100, 100, 100);
  font-size: 16px;
  font-weight: 500;
  width: 100%;
}

.mainDivision_BlogDisplaySection h1 {
  width: 100%;
  font-size: 36px;
}

.mainDivision_BlogDisplaySection h2 {
  width: 100%;
  font-size: 16px;
}

.mainDivision_BlogDisplaySection p {
  width: 100%;
  margin-top: 50px;
  font-size: 15px;
}

/* Responsiveness of this Page */

@media screen and (max-width: 1350px) {
  .mainDivision_BlogDisplaySection {
    width: 100%;
    padding: 25px;
  }
}

/* End of Responsiveness of this Page */
