.ProductsSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}

.mainDivision_ProductSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  gap: 25px;
  flex-wrap: wrap;
}

.ProductsSection .mainHeading_Home {
  color: rgb(80, 80, 80);
  font-size: 28px;
  margin: 10px;
}

/* Responsiveness of this page */

@media screen and (max-width: 1350px) {
  .mainDivision_ProductSection {
    padding: 25px;
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .mainDivision_ProductSection {
    width: 100%;
  }
  .ProductsSection .mainHeading_Home {
    font-size: 24px;
    /* border: 1px solid red; */
    margin: 0;
    margin-top: 25px;
  }
}

/* End of Responsiveness of this page */
