.ProductCard_Home {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  /* border: 1px solid red; */
  height: fit-content;
  width: 300px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
  padding: 10px;
  border-radius: 10px;
}

.mainDivision_ProCard_Home {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  flex-direction: column;
}

.mainDivision_ProCard_Home img {
  width: 100%;
  height: fit-content;
}

.mainDivision_ProCard_Home h1 {
  font-size: 18px;
  font-weight: 600;
  color: rgb(80, 80, 80);
  width: 100%;
}

.mainDivision_ProCard_Home h3 {
  font-size: 15px;
  font-weight: 400;
  color: rgb(80, 80, 80);
  width: 100%;
}

.mainDivision_ProCard_Home .btnsDiv_pCard {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  padding: 25px 0px;
}

.mainDivision_ProCard_Home .btnsDiv_pCard a {
  text-decoration: none;
}
.mainDivision_ProCard_Home .btnsDiv_pCard .callNowBTN_Hero {
  padding: 6% 20px;
  border-radius: 6px;
  border: 2px solid black;
  font-size: 14px;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: black;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 400;
}

.mainDivision_ProCard_Home .btnsDiv_pCard .callNowBTN_Hero:hover {
  background-color: #e5302f;
  border: 2px solid #e5302f;
  color: white;
}

.mainDivision_ProCard_Home .btnsDiv_pCard .whatsAppNowBTN_Hero {
  padding: 6px 20px;
  border-radius: 6px;
  border: 2px solid #25d366;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: #25d366;
  color: white;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.mainDivision_ProCard_Home .btnsDiv_pCard .whatsAppNowBTN_Hero:hover {
  background-color: #198d43;
  border: 2px solid #198d43;
}

.mainDivision_ProCard_Home .btnsDiv_pCard .learnMore_BTN {
  padding: 10px 35px;
  border-radius: 6px;
  border: 2px solid #e5302f;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: transparent;
  color: white;
  color: #e5302f;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.mainDivision_ProCard_Home .btnsDiv_pCard .learnMore_BTN:hover {
  color: white;
  background-color: black;
  border: 2px solid black;
}

/* Responsiveness  */

@media screen and (max-width: 1350px) {
  .ProductCard_Home {
    width: 30%;
    height: fit-content;
  }
}
@media screen and (max-width: 1120px) {
  .ProductCard_Home {
    width: 40%;
    height: fit-content;
  }
}

@media screen and (max-width: 800px) {
  .ProductCard_Home {
    width: 100%;
    height: fit-content;
  }
}

@media screen and (max-width: 500px) {
  .ProductCard_Home {
    max-width: 100%;
    max-height: max-content;
  }
}

/* End of Responsiveness  */
