.UpdatesSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}

.mainDivUpdatesSection {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 1350px;
  /* border: 5px solid rebeccapurple; */
}

.mainDivUpdatesSection a {
  text-decoration: none;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 100%;
}

.blogsSections {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
  background-color: rgb(246, 246, 246);
  padding: 25px;
  margin: 25px 0px;
  border-radius: 10px;
}
.blogsSections h1 {
  width: 100%;
  font-size: 24px;
  font-weight: 600;
  color: rgb(70, 70, 70);
}

.blogsSections p {
  width: 100%;
  font-size: 14px;
  font-weight: 400;
  color: rgb(50, 50, 50);
}

.blogsSections .btnDiv_blogs {
  margin-top: 10px;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.blogsSections .btnDiv_blogs button {
  padding: 6px 20px;
  border-radius: 6px;
  border: 2px solid black;
  font-size: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: black;
  color: white;
  font-weight: 400;
  cursor: pointer;
  transition: 0.2s;
}

.blogsSections .btnDiv_blogs button:hover {
  background-color: #e5302f;
  border: 2px solid #e5302f;
}

/* Responsiveness of This Page */

@media screen and (max-width: 1350px) {
  .mainDivUpdatesSection {
    padding: 25px;

    width: 100%;
  }
  .blogsSections {
    width: 100%;
  }
}
/* End of Responsiveness of This Page */
