.Navbar_MainSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

/* Upper Section Styling */

.Navbar_Section_Upper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: rgb(40, 40, 40);
  color: white;
  padding: 10px 0px;
}

.mainDivision_NavbarSection_Upper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 1350px;
}

.textMachinery_NavbarUpperSection {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
}
.textMachinery_NavbarUpperSection h3 {
  font-size: 14px;
  font-weight: 300;
}

.socialmediaIcons_Navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  gap: 10px;
}
.socialmediaIcons_Navbar a {
  text-decoration: none;
}

.socialmediaIcons_Navbar i {
  color: white;
}

.socialmediaIcons_Navbar i:hover {
  color: #e5302f;
}

/* End of Upper Section Styling */

/* Middle Div Styling */

.NavbarSection_Both {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
}

.Navbar_Section_Middle {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.mainDivision_Navbar_Middle_Division {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
}

.logoDiv_Navbar {
  display: flex;
  justify-content: center;
  width: calc(40% - 50px);
  background-color: white;
  height: 100px;
  padding: 10px 0px;
}

.logoDiv_Navbar a {
  text-decoration: none;
}
.logoDiv_Navbar a img {
  max-width: fit-content;
  max-height: 100%;
}

.contactDetailsDiv_Navbar {
  display: flex;
  justify-content: flex-start;
  width: calc(60% + 50px);
  background-color: #e5302f;
  border-left: 100px solid white;
  border-bottom: 100px solid transparent;
  box-sizing: border-box;
  height: 100px;
  gap: 50px;
}

.contactDetailsDiv_Navbar p {
  position: relative;
  top: 35px;
  gap: 10px;
  font-size: 15px;
  color: rgb(233, 233, 233);
}

.contactDetailsDiv_Navbar p a {
  text-decoration: none;
  color: rgb(233, 233, 233);
}

/* End of Middle Div Styling */

.Navbar_Section_Lower {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  transition: 0.3s;
}

.mainDivision_Navbar_Section_Lower {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 1350px;
  padding: 13px 0px;
  background-color: rgb(40, 40, 40);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.mainDivision_Navbar_Section_Lower ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
}
.mainDivision_Navbar_Section_Lower ul li {
  list-style: none;
}
.mainDivision_Navbar_Section_Lower ul li a {
  text-decoration: none;
  font-size: 14px;
  font-weight: 300;
  gap: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s;
  color: rgb(245, 245, 245);
}
.mainDivision_Navbar_Section_Lower ul li a:hover {
  color: #e5302f;
}

.HideNavBar_Fixed {
  display: none;
}

.Navbar_Section_Lower_New {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
  background-color: white;
  padding: 10px 0px;
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.3);
}

.Navbar_Section_Lower_New .mainDivision_Navbar_Section_Lower_New {
  width: 1350px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logoDiv_New {
  width: 20%;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.logoDiv_New img {
  max-width: 200px;
  max-height: fit-content;
}

.navbarDiv_New {
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbarDiv_New ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.navbarDiv_New ul li {
  list-style: none;
}
.navbarDiv_New ul li a {
  text-decoration: none;
  font-size: 14px;
  font-weight: 400;
  gap: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s;
  color: rgb(20, 20, 20);
}
.navbarDiv_New ul li a:hover {
  color: #e5302f;
}

.contactSalesDiv_New {
  width: 20%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.contactSalesDiv_New button {
  color: white;
  padding: 7px 25px;
  border-radius: 6px;
  border: 2px solid black;
  font-size: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  background-color: black;
  cursor: pointer;
  transition: 0.2s;
  font-weight: 400;
}

.contactSalesDiv_New button:hover {
  background-color: #e5302f;
  border: 2px solid #e5302f;
}

/* Industries Menu Div */

.div_menu_Indistries_hide {
  display: none;
}

.div_menu_2_Indistries_hide {
  display: none;
}

.div_menu_Indistries {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: fit-content;
  padding: 15px 25px;
  background-color: white;
  border-radius: 5px;
  top: 176px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
}

.div_menu_2_Indistries {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: fit-content;
  padding: 15px 25px;
  background-color: white;
  border-radius: 5px;
  top: 50px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
}

.div_menu_Indistries ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  flex-direction: column;
  gap: 10px;
}
.div_menu_2_Indistries ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  flex-direction: column;
  gap: 10px;
}
.div_menu_Indistries ul li {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: fit-content;
  width: 100%;
}
.div_menu_2_Indistries ul li {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: fit-content;
  width: 100%;
}
.div_menu_Indistries ul li a {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  color: rgb(40, 40, 40);
  font-weight: 400;
  font-size: 15px;
}
.div_menu_2_Indistries ul li a {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  color: rgb(40, 40, 40);
  font-weight: 400;
  font-size: 15px;
}

/* End of Industries Menu Div */

/* Mega Menu Navbar */

.MegaMenu_Navbar_Hide {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: fixed;
  flex-direction: column;
  top: -25%;
  background-color: rgb(245, 245, 245);
  transition: 0.3s;
}

.MegaMenu_Navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  position: fixed;
  top: 0;
  background-color: rgb(245, 245, 245);
  transition: 0.3s;
  padding: 50px 0px;
}

.mainDivision_Mega_Menu {
  display: flex;
  justify-content: space-evenly;
  width: 1350px;
  gap: 50px;
  /* border: 1px solid red; */
}

.mainDivision_Mega_Menu span {
  cursor: pointer;
  color: blue;
}

.mainDivision_Mega_Menu .catDivs_MegaMenu {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: fit-content;
}

.mainDivision_Mega_Menu .catDivs_MegaMenu h1 {
  font-size: 22px;
  font-weight: 500;
}
.mainDivision_Mega_Menu .catDivs_MegaMenu ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  flex-direction: column;
  gap: 10px;
}

.mainDivision_Mega_Menu .catDivs_MegaMenu ul li:nth-child(1) {
  margin-top: 15px;
}

.mainDivision_Mega_Menu .catDivs_MegaMenu ul li {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  list-style: none;
}
.mainDivision_Mega_Menu .catDivs_MegaMenu ul li a {
  text-decoration: none;
  font-size: 14px;
  transition: 0.2s;
}

.mainDivision_Mega_Menu .catDivs_MegaMenu ul li a:hover {
  color: #e5302f;
}

/* End of Mega Menu Navbar */

.MobileMenuNav {
  display: none;
}

/* Responsiveness of Navbar */

@media screen and (max-width: 1350px) {
  .mainDivision_NavbarSection_Upper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 0px 25px;
  }

  .mainDivision_Mega_Menu {
    width: 100%;
    padding: 25px;
  }
}

@media screen and (max-width: 900px) {
  .Navbar_MainSection {
    display: none;
  }
  .Navbar_Section_Lower_New {
    display: none;
  }
  .MegaMenu_Navbar_Hide {
    display: none;
  }
  .MegaMenu_Navbar {
    display: none;
  }
  /* Mobile Menu */

  .MobileMenuNav {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 5px 5px;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: +99;
  }

  .mainDiv_MobileMenuNav {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 5px 5px;
  }

  .MobileMenuNav .logoDiv_MobileMenu_Nav {
    width: 50%;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .MobileMenuNav .logoDiv_MobileMenu_Nav img {
    max-height: fit-content;
    max-width: 175px;
  }

  .MobileMenuNav .menuIconDiv_MobMenu_Nav {
    width: 50%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
  }

  /* End of Mobile Menu */
}

/* End of Responsiveness of Navbar */

/* Mobile Menu */

.mobileMenu_View_Navbar {
  background-color: white;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  position: fixed;
  right: 0%;
  transition: 0.5s;
  z-index: +99;
  overflow: auto;
}

.mobileMenu_Hide_Navbar {
  background-color: white;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  position: fixed;
  right: -105%;
  transition: 0.5s;
  z-index: +99;
  overflow: auto;
}

.mainDivisionMobileMenu {
  padding: 25px 25px;
  width: 100%;
}
.mainDivisionMobileMenu ul {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  gap: 10px;
}
.mainDivisionMobileMenu ul li:nth-child(1) {
  color: #393939;
  font-size: 16px;
  font-weight: 600;
}
.mainDivisionMobileMenu ul li:nth-child(3) {
  font-weight: 600;
  margin-top: 10px;
}
.mainDivisionMobileMenu ul li:nth-child(12) {
  font-weight: 600;
  margin-top: 10px;
}

.mainDivisionMobileMenu ul li {
  width: 100%;
  list-style: none;
}
.mainDivisionMobileMenu ul li a {
  text-decoration: none;
  font-size: 13px;
  font-weight: 500;
  color: #5a5a5a;
}

/* Contact Div */

.contactDiv_MobileMenu {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding: 30px 0px;
  padding-bottom: 50px;
}
.contactDiv_MobileMenu h4 {
  font-size: 15px;
  font-weight: 500;
  color: rgb(80, 80, 80);
}
.contactDiv_MobileMenu h1 {
  font-size: 16px;
  font-weight: 600;
  color: rgb(80, 80, 80);
}
.contactDiv_MobileMenu p {
  font-size: 13px;
  color: rgb(80, 80, 80);
}
.contactDiv_MobileMenu p {
  font-size: 13px;
  color: rgb(80, 80, 80);
}
.contactDiv_MobileMenu span a {
  font-size: 14px;
  text-decoration: none;
  color: rgb(80, 80, 80);
}

.contactDiv_MobileMenu span a i {
  color: rgb(80, 80, 80);
}

.socialMediaHandles a i {
  color: #e5302f;
}

/* End of Contact Div */
/* End of Mobile Menu */
